import {useMemo} from "react";
/**
 * @param arr  ,   ,
 * @param filterByKey  ,   ,
 * @param filterStr -  /   
 * @returns script version
 */
export const useFiltredObj = <T>(
  arr: T[],
  filterByKey: keyof T,
  filterStr: string,
) => {
  return useMemo(() => {
    if (!filterStr || !arr) {
      return arr;
    }
    return arr.filter(obj =>
      String(obj[filterByKey]).toLowerCase().includes(filterStr.toLowerCase()),
    );
  }, [filterByKey, arr, filterStr]);
};
